<template>
  <div>
    <div class="detail-box" style="padding-bottom:40px;">
      <h2>流量分析</h2>
      <h3>是指在获得网站访问量基本数据的情况下对有关数据进行统计、分析，从中发现用户访问网站的规律，并将这些规律与网络营销策略等相结合，从而发现目前网络营销活动中可能存在的问题，并为进一步修正或重新制定网络营销策略提供依据。当然这样的定义是站在网络营销管理的角度来考虑的</h3>
      <div class="detail-box-left">
        <span>购买数量：</span>
        <record number=1 @record-event="onRecordEvent"></record>
      </div>
      <div class="detail-box-left">
        <span>产品类型：</span>
        <selection :selectParam="selectParam" @select-val="onSelectVal"></selection>
      </div>
      <div class="detail-box-left">
        <span>有效时间：</span>
        <checkbox :checkboxParam="radioParam" @checkbox-val="onRadioVal"></checkbox>
      </div>
      <div class="detail-box-left">
        <span>产品版本：</span>
        <checkbox :checkboxParam="checkboxParam" @checkbox-val="onCheckBoxVal"></checkbox>
      </div>
      <div class="detail-box-left"><span>总价：</span> 678元</div>
      <div class="detail-box-left"><span></span> <button @click="isLayout=true;isShadow=true;state='count'">立即购买</button></div>
    </div>

    <mask-layer v-show="isShadow" @click.native="isShadow=false;isLayout=false;isCheck=false;isSuccess=false;isFail=false"></mask-layer>
    
    <layout :isLayout="isLayout" :state="state" @close-layout="onCloseLayout">
      <div slot="count" class="count-layer">
        <table>
          <tr>
            <th>购买数量</th>
            <th>产品类型</th>
            <th>有效时间</th>
            <th>产品版本</th>
            <th>总价</th>
          </tr>
          <tr>
            <td>{{gridData[0].num}}</td>
            <td>{{gridData[0].type}}</td>
            <td>{{gridData[0].time}}</td>
            <td>{{gridData[0].version}}</td>
            <td>{{gridData[0].price}}</td>
          </tr>
        </table>
        <button @click="isLayout=false;state='check';isCheck=true">确认购买</button>
      </div>
    </layout>

    <layout :isLayout="isCheck" :state="state" @close-layout="onCheckLayout">
      <div slot="check" class="check-layer count-layer">
        <p>请检查你的支付状态！</p>
        <button @click="goRoute">支付成功</button>
        <button @click="isCheck=false;state='fail';isFail=true">支付失败</button>
      </div>
    </layout>

    <layout :isLayout="isSuccess" :state="state" @close-layout="onSuccessLayout">
      <div slot="success">
        <p>购买成功！{{num}}s后页面跳转...</p>
      </div>
    </layout>

    <layout :isLayout="isFail" :state="state" @close-layout="onFailLayout">
      <div slot="fail">
        <p>购买失败！</p>
      </div>
    </layout>

    <div class="detail-box">
      <h2>产品说明</h2>
      <p>网站访问统计分析报告的基础数据源于网站流量统计信息，但其价值远高于原始数据资料。专业的网站访问统计分析报告对网络营销的价值，正如专业的财务分析报告对企业经营策略的价值</p> 
      <h4>用户行为指标</h4>  
      <p>
        用户行为指标主要反映用户是如何来到网站的、在网站上停留了多长时间、访问了哪些页面等，主要的统计指标包括：<br/>
        用户在网站的停留时间；<br/>
        用户来源网站（也叫“引导网站”）；<br/>
        用户所使用的搜索引擎及其关键词；<br/>
        在不同时段的用户访问量情况等。
      </p> 
      <h4>浏览网站方式</h4>  
      <p>
        用户上网设备类型<br/>
        用户浏览器的名称和版本<br/>
        访问者电脑分辨率显示模式<br/>
        用户所使用的操作系统名称和版本<br/>
        用户所在地理区域分布状况等
      </p> 
    </div>
  </div>
</template>

<script>
import record from '../components/record'
import selection from '../components/selection'
import checkbox from '../components/checkbox'
import layout from '../components/layout'
import maskLayer from '../components/maskLayer'
export default {
  data: function () {
    return {
      selectParam: ['入门版','中级版','高级版'],
      radioParam: [
        {
          title: '半年',
          id: 'half',
          type: 'radio',
          name: 'radio-box'
        },
        {
          title: '一年',
          id: 'one',
          type: 'radio',
          name: 'radio-box'
        },
        {
          title: '三年',
          id: 'three',
          type: 'radio',
          name: 'radio-box'
        }
      ],
      checkboxParam: [
        {
          title: '客户版',
          id: 'kehu',
          type: 'checkbox',
          name: ''
        },
        {
          title: '代理商版',
          id: 'daili',
          type: 'checkbox',
          name: ''
        },
        {
          title: '专家版',
          id: 'zhuanjia',
          type: 'checkbox',
          name: ''
        }
      ],
      gridData: [
        {
          num: 1,
          type:'入门版',
          time: '半年',
          version: '客户版',
          price: 500
        }
      ],
      isShadow: false,
      isLayout: false,
      isCheck: false,
      isSuccess: false,
      isFail: false,
      state: '',
      num: 5
    }
  },
  components: {
    record,
    selection,
    checkbox,
    layout,
    maskLayer
  },
  created: function(){
    this.menu();
  },
  methods: {
    menu: function(){
      window.scrollTo(0,0);
    },
    onRadioVal: function(pra){
      this.gridData[0].time = pra.join(",");
    },
    onCheckBoxVal: function(pra){
      this.gridData[0].version = pra.join(",");
    },
    onSelectVal: function(pra){
      this.gridData[0].type = pra;
    },
    onCloseLayout: function(pra){
      this.isLayout = pra;
      this.isShadow = pra;
    },
    onCheckLayout: function(pra){
      this.isCheck = pra;
      this.isShadow = pra;
    },
    onSuccessLayout: function(pra){
      this.isSuccess = pra;
      this.isShadow = pra;
    },
    onFailLayout: function(pra){
      this.isFail = pra;
      this.isShadow = pra;
    },
    onRecordEvent: function(pra){
      this.gridData[0].num = pra;
    },
    goRoute: function(){
      var self = this;
      let time = null;
      this.isCheck=false;
      this.state='success';
      this.isSuccess=true;
      time = setInterval(function(){
        if(self.num <= 0){
          clearInterval(time);
          self.$router.push('/oraderList');
        }else {
          self.num --;
        }
      },1000);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
</style>



