<template>
  <div class="app-head clearfix">
    <div class="app-head-inner pull-left">
      <router-link to="/" class="head-logo">
        <img src="../assets/logo.png">
      </router-link> 
    </div>
    <ul class="pull-right clearfix">
      <li @click="isLayout=true;isShadow=true;state='login'">登录</li>
      <li>|</li>
      <li @click="isLayout=true;isShadow=true;state='register'">注册</li>
      <li>|</li>
      <li @click="isLayout=true;isShadow=true;state='about'">关于</li>
    </ul>

    <mask-layer v-show="isShadow" @click.native="isShadow = false;isLayout=false"></mask-layer>

    <layout :isLayout="isLayout" :state="state" @close-layout="onCloseLayout">
      <div slot="login" class="login">
        <div>
          <label>用户名：</label>
          <input type="text" placeholder="请输入用户名" />
        </div>
        <div>
          <label>密码：</label>
          <input type="text" placeholder="请输入密码"/>
        </div>
        <div>
          <label>&nbsp;</label>
          <button>登 录</button>
        </div>
      </div>
      <div slot="register" class="register login">
        <div>
          <label>用户名：</label>
          <input type="text" placeholder="请输入用户名" />
        </div>
        <div>
          <label>密码：</label>
          <input type="text" placeholder="请输入密码"/>
        </div>
        <div>
          <label>&nbsp;</label>
          <button>注 册</button>
        </div>
      </div>
      <div slot="about" class="about">
        <p>本报告在调研数据的基础上，采用定性与定量相结合的方式深入分析了专车市场发展的驱动因素与阻碍因素、专车市场背后的产业格局、专车企业的竞争格局、用户对专车市场的依赖程度、专车对其他交通工具运力的补充效应等，通过这五个章节的研究反映专车市场的发展态势和面临的问题。报告力求客观、深入、准确地反映中国专车市场发展情况，为政府、企事业单位和社会各界提供决策依据。</p>
      </div>
    </layout>
  </div>
</template>

<script>
import layout from '../components/layout'
import maskLayer from '../components/maskLayer'
export default {
  data: function () {
    return {
      isShadow: false,
      isLayout: false,
      state: ''
    }
  },
  components: {
    layout,
    maskLayer
  },
  methods: {
    onCloseLayout: function(pra){
      this.isLayout = pra;
      this.isShadow = pra;
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

.app-head {
  width: 1200px;
  height: 90px;
  margin:0 auto;
  line-height: 90px;
}

.app-head ul li {
  float: left;
  color:#b2b2b2;
  margin-left: 10px;
  cursor: pointer;
}

.app-head-inner img{
  vertical-align: middle;
  width: 55px;
}

.el-dialog__body {
  padding-bottom: 0;
}

.login div+div {
  margin-top: 25px;
}

.login label {
  display: inline-block;
  width: 80px;
  text-align: left;
}

.login input {
  width: 300px;
  height: 34px;
  border:1px solid #ccc;
  padding-left: 10px;
}

.login button {
  width: 70px;
  height: 42px;
  line-height: 42px;
  background: #4fc08d;
  color:#fff;
  font-size: 16px;
}

.about p {
  line-height: 1.6;
  text-indent: 2em;
}
</style>



