<template>
  <div @click="showList" class="selection-box">
    <span>{{selectParam[0]}}</span>
    <div id="square"></div>
    <ul>
      <li @click.stop="txtChange($event)" v-for="(item,index) in selectParam">{{item}}</li>
    </ul>   
  </div>
</template>

<script>
export default {
  props:['selectParam'],
  data: function () {
    return {
    }
  },
  methods: {
    showList: function(){
      $('.selection-box ul').toggle()
    },
    txtChange: function(event){
      let val = event.currentTarget.innerHTML;
      let selectBox = '';
      $(".selection-box span").html(val);
      $('.selection-box ul').hide();
      selectBox = $(".selection-box span").html();
      this.$emit('select-val',selectBox);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
.selection-box {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.selection-box>span {
  display: inline-block;
  border:1px solid #e3e3e3;
  padding:3px 25px 3px 10px;
  border-radius: 5px;
  background-color: #fff;
}

#square {
  position: absolute;
  top:50%;
  right:5px;
  width: 0;
  height: 0;
  border-width: 4px;
  border-color: #e3e3e3 transparent transparent transparent;
  border-style: solid dashed dashed dashed;
  margin-top: -2px;
}

.selection-box ul {
  display: none;
  position: absolute;
  width: 100%;
  top:100%;
  border:1px solid #e3e3e3;
  border-top:none;
  background: #fff;
  z-index: 100;
}

.selection-box ul li {
  padding:3px 0 3px 10px;
  cursor: pointer;
}

.selection-box ul li:hover {
  background-color: #e3e3e3;
}
</style>



