<template>
  <div id="app-swiper">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div v-for="item in imgList" class="swiper-slide">
          <img :src="item.url"/>
        </div>
      </div>
      <div class="swiper-button-next"></div>
      <div class="swiper-button-prev"></div>
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
import Swiper from '../../static/swiper-3.4.2.min.js'
export default {
  data: function () {
    return {
      imgList:[
        {
          url:require('../assets/slideShow/pic1.jpg')
        },
        {
          url:require('../assets/slideShow/pic2.jpg')
        },
        {
          url:require('../assets/slideShow/pic3.jpg')
        },
        {
          url:require('../assets/slideShow/pic4.jpg')
        }
      ]
    }
  },
  mounted:function(){
    this.lunbo()
  },
  methods: {
    lunbo:function(){
      new Swiper('.swiper-container', {
        prevButton:'.swiper-button-prev',
        nextButton:'.swiper-button-next',
        spaceBetween: 30,
        pagination : '.swiper-pagination',
        paginationClickable :true,
        loop : true,
        autoplay : 2000
      });
    }
  }
}
</script>

<style>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("../assets/swiper-3.4.2.min.css"); 
#app-swiper {
  width: 900px;
  height: 500px;
  overflow: hidden;
}

#app-swiper img {
  width: 100%;
}
</style>



