<template>
  <div>
    <div class="detail-box" style="padding-bottom:40px;">
      <h2>数据统计</h2>
      <h3>历史资料、科学实验、检验、统计等所获得的和用于科学研究、技术设计、查证、决策等的数值加以统计为解决方案做前期准备</h3>
      <div class="detail-box-left">
        <span>产品类型：</span>
        <checkbox :checkboxParam="checkboxParam" @checkbox-val="onCheckBoxVal"></checkbox>
      </div>
      <div class="detail-box-left">
        <span>适用地区：</span>
        <selection :selectParam="selectParam" @select-val="onSelectBox"></selection>
      </div>
      <div class="detail-box-left">
        <span>有效时间：</span> 半年
      </div>
      <div class="detail-box-left">
        <span>总价：</span> 500元
      </div>
      <div class="detail-box-left">
        <span>&nbsp;</span> <button @click="isLayout=true;isShadow=true;state='count'">立即购买</button>
      </div>
    </div>

    <mask-layer v-show="isShadow" @click.native="isShadow = false;isLayout=false;isCheck=false;isSuccess=false;isFail=false"></mask-layer>
    
    <layout :isLayout="isLayout" :state="state" @close-layout="onCloseLayout">
      <div slot="count" class="count-layer">
        <table>
          <tr>
            <th>产品类型</th>
            <th>适用地区</th>
            <th>有效时间</th>
            <th>总价</th>
          </tr>
          <tr>
            <td>{{gridData[0].type}}</td>
            <td>{{gridData[0].address}}</td>
            <td>{{gridData[0].time}}</td>
            <td>{{gridData[0].price}}</td>
          </tr>
        </table>
        <button @click="isLayout=false;state='check';isCheck=true">确认购买</button>
      </div>
    </layout>

    <layout :isLayout="isCheck" :state="state" @close-layout="onCheckLayout">
      <div slot="check" class="check-layer count-layer">
        <p>请检查你的支付状态！</p>
        <button @click="goRoute">支付成功</button>
        <button @click="isCheck=false;state='fail';isFail=true">支付失败</button>
      </div>
    </layout>

    <layout :isLayout="isSuccess" :state="state" @close-layout="onSuccessLayout">
      <div slot="success">
        <p>购买成功！{{num}}s后页面跳转...</p>
      </div>
    </layout>

    <layout :isLayout="isFail" :state="state" @close-layout="onFailLayout">
      <div slot="fail">
        <p>购买失败！</p>
      </div>
    </layout>

    <div class="detail-box" style="padding-bottom:20px">
      <h2>产品说明</h2>
      <h3>历史资料、科学实验、检验、统计等所获得的和用于科学研究、技术设计、查证、决策等的数值加以统计为解决方案做前期准备。</h3>
      <table>
        <tr>
          <th>安全安保</th>
          <th>办公文教</th>
          <th>彩票</th>
          <th>车辆物流</th>
          <th>成人用品</th>
          <th>出版传媒</th>
          <th>电脑硬件</th>
        </tr>
        <tr>
          <th>电子电工</th>
          <th>房地产建筑装修</th>
          <th>分类平台</th>
          <th>服装鞋帽</th>
          <th>箱包饰品</th>
          <th>化工原料制品</th>
          <th>机械设备</th>
        </tr>
        <tr>
          <th>家庭日用品</th>
          <th>家用电器</th>
          <th>教育培训</th>
          <th>节能环保</th>
          <th>金融服务</th>
          <th>礼品</th>
          <th>旅游住宿</th>
        </tr>
        <tr>
          <th>美容化妆</th>
          <th>母婴护理</th>
          <th>农林牧渔</th>
          <th>软件</th>
          <th>商务服务</th>
          <th>生活服务</th>
          <th>食品保健品</th>
        </tr>
        <tr>
          <th>手机数码</th>
          <th>通讯服务设备</th>
          <th>网络服务</th>
          <th>医疗服务</th>
          <th>游戏</th>
          <th>运动休闲娱乐</th>
          <th>招商加盟</th>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import checkbox from '../components/checkbox'
import selection from '../components/selection'
import layout from '../components/layout'
import maskLayer from '../components/maskLayer'
export default {
  data: function () {
    return {
      checkboxParam: [
        {
          title: '红色版',
          id: 'red',
          type: 'radio',
          name: 'radio-box'
        },
        {
          title: '绿色版',
          id: 'green',
          type: 'radio',
          name: 'radio-box'
        },
        {
          title: '紫色版',
          id: 'violet',
          type: 'radio',
          name: 'radio-box'
        }
      ],
      selectParam: ['北京','上海','广州','天津','武汉','重庆'],
      product:'',
      gridData: [
        {
          type: '红色版',
          address:'北京',
          time: '半年',
          price: '500'
        }
      ],
      isShadow: false,
      isLayout: false,
      isCheck: false,
      isSuccess: false,
      isFail: false,
      state: '',
      num: 5
    }
  },
  components: {
    checkbox,
    selection,
    layout,
    maskLayer
  },
  created: function(){
    this.menu();
  },
  methods: {
    menu: function(){
      window.scrollTo(0,0);
    },
    onCheckBoxVal: function(pra){
      this.gridData[0].type = pra.join(",");
    },
    onSelectBox: function(pra){
      this.gridData[0].address = pra;
    },
    onCloseLayout: function(pra){
      this.isLayout = pra;
      this.isShadow = pra;
    },
    onCheckLayout: function(pra){
      this.isCheck = pra;
      this.isShadow = pra;
    },
    onSuccessLayout: function(pra){
      this.isSuccess = pra;
      this.isShadow = pra;
    },
    onFailLayout: function(pra){
      this.isFail = pra;
      this.isShadow = pra;
    },
    goRoute: function(){
      let self = this;
      let time = null;
      this.isCheck=false;
      this.state='success';
      this.isSuccess=true;
      time = setInterval(function(){
        if(self.num <= 0){
          clearInterval(time);
          self.$router.push('/oraderList');
        }else {
          self.num --;
        }
      },1000);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
.detail-box table th {
  padding-left: 15px;
  border:1px solid #f0f2f5;
  height: 46px;
  line-height: 44px;
}
</style>



