import Vue from 'vue'
import App from './App'
import VRouter from 'vue-router'
import AppDetail from './pages/appDetail'
import count from './pages/count'
import publish from './pages/publish'
import forecast from './pages/forecast'
import analysis from './pages/analysis'
import appContent from './pages/appContent'
import oraderList from './pages/oraderList'
//引入jq
import $ from 'jquery'

//引入Element
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
Vue.use(ElementUI)

// 设置路由
Vue.use(VRouter)
let router = new VRouter({
	routes: [
		{
			path: '/detail',
			component: AppDetail,
			redirect: '/detail/count',
			children: [
				{
					path: 'count',
					name: 'count',
					component: count
				},
				{
					path: 'publish',
					name: 'publish',
					component: publish
				},
				{
					path: 'forecast',
					name: 'forecast',
					component: forecast
				},
				{
					path: 'analysis',
					name: 'analysis',
					component: analysis
				}
			]
		},
		{
			path: '/',
			component: appContent
		},
		{
			path: '/oraderList',
			component: oraderList
		}
	]
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  router,
  components: { App }
})



