<template>
  <div class="detail clearfix">
    <dl class="pull-left">
      <dt><img :src="url" alt=""/></dt>
      <router-link to="/detail/count" tag="dd" :class="$route.name == 'count'?'green':''">数据统计</router-link>
      <router-link to="/detail/forecast" tag="dd" :class="$route.name == 'forecast'?'green':''">数据预测</router-link>
      <router-link to="/detail/analysis" tag="dd" :class="$route.name == 'analysis'?'green':''">流量分析</router-link>
      <router-link to="/detail/publish" tag="dd" :class="$route.name == 'publish'?'green':''">广告发布</router-link>
    </dl>
    <div class="pull-right detail-content">
      <router-view></router-view>
    </div>
    {{changeurl}}
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      url:require('../assets/images/1.png')
    }
  },
  computed: {
    changeurl: function(){
      var imgurl = this.$route.name;
      switch(imgurl){
        case 'count':
          this.url = require('../assets/images/1.png');
          break;
        case 'forecast':
          this.url = require('../assets/images/2.png');
          break;
        case 'analysis':
          this.url = require('../assets/images/3.png');
          break;
        case 'publish':
          this.url = require('../assets/images/4.png');
          break;
      }
    }
  }
}
</script>

<style scoped>
.detail dl {
  width: 200px;
  background-color: #fff;
  box-shadow: 0 0 1px #ddd;
  padding-bottom: 20px;
  margin-right: 20px;
}

.detail dl dt {
  text-align: center;
  padding:20px 0;
}

.detail dl dd {
  padding:7px 0 7px 15px;
  cursor: pointer;
}

.detail dl dd:hover {
  background-color: #4fc08d;
  color:#fff;
}

.green {
  background-color: #4fc08d;
  color:#fff;
}
</style>

<style>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
.detail-content {
  width: 980px;
}

.detail-box {
  background: #fff;
  padding:0 20px;
}

.detail-box + .detail-box {
  margin-top: 20px;
  padding-bottom: 15px;
}

.detail-box h2 {
  font-size: 20px;
  padding:20px 0;
}

.detail-box h3 {
  background-color: #fcfcff;
  padding:10px 0;
  color:#999;
  line-height: 1.8;
  margin-bottom: 20px;
}

.detail-box h4 {
  font-size: 18px;
  font-weight: bold;
  padding:20px 0 10px 0;
}

.detail-box p {
  line-height: 1.7;
  margin-bottom: 20px;
}

.detail-box .detail-box-left {
  margin-bottom: 20px;
}

.detail-box .detail-box-left>span {
  display: inline-block;
  width: 105px;
  text-align: left;
}

.detail-box button {
  display: inline-block;
  padding:10px 15px;
  color:#fff;
  background: #4fc08d;
  text-align: center;
  cursor: pointer;
}

table {
  width: 100%;
}

.count-layer button {
  display: inline-block;
  padding:10px 15px;
  color:#fff;
  background: #4fc08d;
  text-align: center;
  cursor: pointer;
}

.count-layer table {
  margin-bottom: 30px;
  text-align: center;
}

.count-layer table th {
  background-color: #4fc08d;
  color:#fff;
  height: 30px;
  border:1px solid #4fc08d;
}

.count-layer table td{
  border:1px solid #ccc;
  height: 30px;
}

.check-layer p {
  margin-bottom: 20px;
}
</style>



