<template>
  <transition name="layer">
    <div v-show="isLayout" class="layout-box">
      <div id="del" @click="close">x</div>
      <slot :name="state"></slot>
    </div>
  </transition>
</template>

<script>
export default {
  props: ['isLayout','state'],
  data: function () {
    return {
      isClose: false
    }
  },
  methods: {
    close:function(){
      this.$emit('close-layout',this.isClose);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/ 
.layout-box {
  position: fixed;
  top:50%;
  left:50%;
  margin-left: -250px;
  transform: translateY(-50%);
  width: 500px;
  border:2px solid #464068;
  padding:40px 30px;
  background-color: #fff;
  z-index:1000;
  line-height: 1;
}

#del {
  position: absolute;
  right: 10px;
  top:10px;
  width: 15px;
  height: 15px;
  cursor: pointer;
  text-align: right;
}

#del:hover {
  color:#4fc08d;
}

.layer-enter-active,.layer-leave-active {
  transition: all .3s ease;
}

.layer-leave-active {
  transition: all .1s linear;
}

.layer-enter,.layer-leave-active {
  top:0;
  transform: translateY(-100%);
}

</style>



