<template>
  <div>
    <div class="detail-box" style="padding-bottom:40px;">
      <h2>数据预测</h2>
      <h3>未来，大数据会变得越来越重要，其核心应用预测也会成为互联网行业以及产业变革的重要力量，我们很有必要对数据预测及其分析方法进行全面且深入的了解。在这一点上，《大数据预测》是本很好的读物，适合大数据所有相关行业的人阅读。</h3>
      <div class="detail-box-left">
        <span>购买数量：</span>
        <record number=20 @record-event="onRecordEvent"></record>
      </div>
      <div class="detail-box-left">
        <span>媒介：</span>
        <checkbox :checkboxParam="checkboxParam" @checkbox-val="onCheckBoxVal"></checkbox>
      </div>
      <div class="detail-box-left">
        <span>有效时间：</span> 一年
      </div>
      <div class="detail-box-left">
        <span>总价：</span> 500元
      </div>
      <div class="detail-box-left">
        <span>&nbsp;</span> <button @click="isLayout=true;isShadow=true;state='count'">立即购买</button>
      </div>
    </div>

    <mask-layer v-show="isShadow" @click.native="isShadow=false;isLayout=false;isCheck=false;isSuccess=false;isFail=false"></mask-layer>
    
    <layout :isLayout="isLayout" :state="state" @close-layout="onCloseLayout">
      <div slot="count" class="count-layer">
        <table>
          <tr>
            <th>购买数量</th>
            <th>媒介</th>
            <th>有效时间</th>
            <th>总价</th>
          </tr>
          <tr>
            <td>{{gridData[0].num}}</td>
            <td>{{gridData[0].media}}</td>
            <td>{{gridData[0].time}}</td>
            <td>{{gridData[0].price}}</td>
          </tr>
        </table>
        <button @click="isLayout=false;state='check';isCheck=true">确认购买</button>
      </div>
    </layout>

    <layout :isLayout="isCheck" :state="state" @close-layout="onCheckLayout">
      <div slot="check" class="check-layer count-layer">
        <p>请检查你的支付状态！</p>
        <button @click="goRoute">支付成功</button>
        <button @click="isCheck=false;state='fail';isFail=true">支付失败</button>
      </div>
    </layout>

    <layout :isLayout="isSuccess" :state="state" @close-layout="onSuccessLayout">
      <div slot="success">
        <p>购买成功！{{num}}s后页面跳转...</p>
      </div>
    </layout>

    <layout :isLayout="isFail" :state="state" @close-layout="onFailLayout">
      <div slot="fail">
        <p>购买失败！</p>
      </div>
    </layout>

    <div class="detail-box">
      <h2>产品说明</h2>
      <p>2020年的一天，在你驱车前往公司的路上，导航系统通过预测交通流量，会自动帮你选择一条最合适的交通路线；车内推荐系统会根据你的饮食习惯预测你可能会喜欢吃什么，并推荐沿途的早餐店；你的电子社交助理已经为你自动选择了你可能感兴趣的社交网信息；当车内系统预测到你驾车有些分心时，座椅会自动震动进行提醒…… 以上这些情景不是科幻大片独有的，它们有的已经或会在未来的某一天成为现实。而这一切所倚靠的就是预测分析技术。 大数据时代下，作为其核心，预测分析已在商业和社会中得到广泛应用。随着越来越多的数据被记录和整理，未来预测分析必定会成为所有领域的关键技术。 作为预测分析领域的专家，埃里克·西格尔博士深谙预测分析界已经实现和正在发生的事情、面临的问题和将来可能的前景。在《大数据预测》一书中，他结合预测分析的应用实例，对其进行了深入、细致且全面的解读。 关于预测分析，你想了解的全部，你的生活以及这个世界会因为预测分析改变到什么程度，《大数据预测》都会告诉你。</p>    
    </div>
  </div>
</template>

<script>
import record from '../components/record'
import checkbox from '../components/checkbox'
import layout from '../components/layout'
import maskLayer from '../components/maskLayer'
export default {
  data: function () {
    return {
      checkboxParam: [
        {
          title: '纸质报告',
          id: 'paper',
          type: 'checkbox',
          name: ''
        },
        {
          title: 'pdf',
          id: 'pdf',
          type: 'checkbox',
          name: ''
        },
        {
          title: '页面报告',
          id: 'page',
          type: 'checkbox',
          name: ''
        },
        {
          title: '邮件',
          id: 'email',
          type: 'checkbox',
          name: ''
        }
      ],
      gridData: [
        {
          num: 20,
          media:'纸质报告',
          time: '一年',
          price: 500
        }
      ],
      isShadow: false,
      isLayout: false,
      isCheck: false,
      isSuccess: false,
      isFail: false,
      state: '',
      num: 5
    }
  },
  components: {
    record,
    checkbox,
    layout,
    maskLayer
  },
  created: function(){
    this.menu();
  },
  methods: {
    menu: function(){
      window.scrollTo(0,0);
    },
    onCheckBoxVal: function(pra){
      this.gridData[0].media = pra.join(",");
    },
    onCloseLayout: function(pra){
      this.isLayout = pra;
      this.isShadow = pra;
    },
    onCheckLayout: function(pra){
      this.isCheck = pra;
      this.isShadow = pra;
    },
    onSuccessLayout: function(pra){
      this.isSuccess = pra;
      this.isShadow = pra;
    },
    onFailLayout: function(pra){
      this.isFail = pra;
      this.isShadow = pra;
    },
    onRecordEvent: function(pra){
      this.gridData[0].num = pra;
    },
    goRoute: function(){
      var self = this;
      let time = null;
      this.isCheck=false;
      this.state='success';
      this.isSuccess=true;
      time = setInterval(function(){
        if(self.num <= 0){
          clearInterval(time);
          self.$router.push('/oraderList');
        }else {
          self.num --;
        }
      },1000);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
</style>



