<template>
  <div class="checkbox-box clearfix">
     <div v-for="(item,index) in checkboxParam">
      <input :checked="index == 0?true:false" :id="item.id" :type="item.type":name="item.name" :value="item.title" @change="val"/>
      <label :for="item.id">{{item.title}}</label>
    </div> 
  </div>
</template>

<script>
export default {
  props:['checkboxParam'],
  methods: {
    val: function(){
      var self = this;
      var checkboxBox = [];
      var type = this.checkboxParam[0].type;
      $(".checkbox-box input[type="+type+"]").each(function(index){
        if($(".checkbox-box input[type="+type+"]").eq(index).is(':checked')){
          checkboxBox.push($(".checkbox-box input[type="+type+"]").eq(index).val());
        }
      });
      self.$emit('checkbox-val',checkboxBox);
    } 
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
.checkbox-box {
  display: inline-block;
  vertical-align: middle;
}

.checkbox-box>div {
  position: relative;
  float:left;
  margin-right: 5px;
}

.checkbox-box label {
  padding:3px 10px;
  border:1px solid #e3e3e3;
  border-radius: 5px;
  cursor: pointer;
}

.checkbox-box input {
  position: absolute;
  left:50%;
  z-index:-1;
}

.checkbox-box input:checked+label {
  background: #4fc08d;
  color:#fff;
  border-color:#4fc08d;
}

</style>



