<template>
  <div class="record-box clearfix">
    <div class="del" @click="del">-</div>
    <div class="record-val">{{number}}</div>
    <div class="add" @click="add">+</div>   
  </div>
</template>

<script>
export default {
  props: ['number'],
  data: function () {
    return {
      
    }
  },
  methods: {
    del: function(){
      let num = $(".record-val").html();
      if(num <= 1){
        num = 1;
      }else {
        num --;
      }
      $(".record-val").html(num);
      this.$emit('record-event',num);
    },
    add: function(){
      let num = $(".record-val").html();
      num ++;
      $(".record-val").html(num);
      this.$emit('record-event',num);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
.record-box {
  display: inline-block;
  vertical-align: middle;
}

.record-box div {
  float: left;
  width: 27px;
  height: 27px;
  line-height: 25px;
  text-align: center;
  border:1px solid #e3e3e3;
  margin-left: -1px;
}

.del,.add {
  cursor: pointer;
}

.del:hover,.add:hover {
  background: #4fc08d;
  color:#fff;
  border-color: #4fc08d;
}

</style>



