<template>
  <div class="oraderList">
    <h2>您的产品</h2>
    <div style="margin-bottom:20px">
      <span>选择产品：</span>
      <selection :selectParam="selectParam" @select-val="onSelectBox"></selection>
      <span style="margin-left:15px">开始日期：</span>
      <el-date-picker
        v-model="value1"
        type="date"
        placeholder="选择日期"
        size="small"
      >
      </el-date-picker>
      <span style="margin-left:15px">结束日期：</span>
      <el-date-picker
        v-model="value2"
        type="date"
        placeholder="选择日期"
        size="small"
      >
      </el-date-picker>
      <span style="margin-left:15px">关键词：</span>
      <input class="keyword" type="text"/>
    </div>
    <div style="padding-bottom:200px">
      <table>
        <tr>
          <th>订单号</th>
          <th>购买产品</th>
          <th>版本类型</th>
          <th>有效时间</th>
          <th>购买日期</th>
          <th>数量</th>
          <th>总价</th>
        </tr>
        <tr>
          <td>ddj123</td>
          <td>数据统计</td>
          <td>高级版</td>
          <td>1年</td>
          <td>2016-10-10</td>
          <td>2</td>
          <td>500元</td>
        </tr>
        <tr>
          <td>yuj583</td>
          <td>流量分析</td>
          <td>户外版</td>
          <td>3个月</td>
          <td>2016-5-2</td>
          <td>1</td>
          <td>2200元</td>
        </tr>
        <tr>
          <td>pmd201</td>
          <td>广告发布</td>
          <td>商铺版</td>
          <td>3年</td>
          <td>2016-8-3</td>
          <td>12</td>
          <td>7890元</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import selection from '../components/selection'
export default {
  data: function(){
    return {
      selectParam: ['数据统计','数据预测','流量分析','广告发布'],
      gridData: [
        {
          type: '红色版',
          address:'北京',
          time: '半年',
          price: '500'
        }
      ],
      value1: '',
      value2: ''
    }
  },
  components: {
    selection
  },
  methods: {
    onSelectBox: function(pra){
      this.gridData[0].address = pra;
    },
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/ 
.oraderList h2 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}

.keyword {
  width: 200px;
  height: 32px;
  line-height: 30px;
  padding-left: 10px;
  border:1px solid #ccc;
}

table {
  width: 100%;
}

table th {
  background-color: #4fc08d;
  color:#fff;
  height: 30px;
  border:1px solid #4fc08d;
}

table td {
  height: 40px;
  line-height: 40px;
  text-align: center;
  border:1px solid #ccc;
}
</style>



