<template>
  <div>
    <div class="detail-box" style="padding-bottom:40px;">
      <h2>广告发布</h2>
      <h3>广告活动按照广告计划执行，到完成广告创作并形成广告作品之后，经过广告主的最后审核同意，即可送到预定的媒介发布刊播。这项工作一般由媒介部门的有关专业人员负责，他们的任务就是专门负责与有关媒介单位接洽，安排有关广告的发播事宜，并对发播质量实施监督。</h3>
      <div class="detail-box-left">
        <span>购买数量：</span>
        <record number=20 @record-event="onRecordEvent"></record>
      </div>
      <div class="detail-box-left">
        <span>行业：</span>
        <selection :selectParam="selectParam" @select-val="onSelectVal"></selection>
      </div>
      <div class="detail-box-left">
        <span>产品版本：</span>
        <checkbox :checkboxParam="checkboxParam" @checkbox-val="onCheckBoxVal"></checkbox>
      </div>
      <div class="detail-box-left">
        <span>有效时间：</span> 半年
      </div>
      <div class="detail-box-left">
        <span>总价：</span> 500元
      </div>
      <div class="detail-box-left">
        <span>&nbsp;</span> <button @click="isLayout=true;isShadow=true;state='count'">立即购买</button>
      </div>
    </div>

    <mask-layer v-show="isShadow" @click.native="isShadow=false;isLayout=false;isCheck=false;isSuccess=false;isFail=false"></mask-layer>
    
    <layout :isLayout="isLayout" :state="state" @close-layout="onCloseLayout">
      <div slot="count" class="count-layer">
        <table>
          <tr>
            <th>购买数量</th>
            <th>行业</th>
            <th>产品版本</th>
            <th>有效时间</th>
            <th>总价</th>
          </tr>
          <tr>
            <td>{{gridData[0].num}}</td>
            <td>{{gridData[0].type}}</td>
            <td>{{gridData[0].version}}</td>
            <td>{{gridData[0].time}}</td>
            <td>{{gridData[0].price}}</td>
          </tr>
        </table>
        <button @click="isLayout=false;state='check';isCheck=true">确认购买</button>
      </div>
    </layout>

    <layout :isLayout="isCheck" :state="state" @close-layout="onCheckLayout">
      <div slot="check" class="check-layer count-layer">
        <p>请检查你的支付状态！</p>
        <button @click="goRoute">支付成功</button>
        <button @click="isCheck=false;state='fail';isFail=true">支付失败</button>
      </div>
    </layout>

    <layout :isLayout="isSuccess" :state="state" @close-layout="onSuccessLayout">
      <div slot="success">
        <p>购买成功！{{num}}s后页面跳转...</p>
      </div>
    </layout>

    <layout :isLayout="isFail" :state="state" @close-layout="onFailLayout">
      <div slot="fail">
        <p>购买失败！</p>
      </div>
    </layout>

    <div class="detail-box" style="padding-bottom:20px">
      <h2>产品说明</h2>
      <p>广告活动按照广告计划执行，到完成广告创作并形成广告作品之后，经过广告主的最后审核同意，即可送到预定的媒介发布刊播。这项工作一般由媒介部门的有关专业人员负责，他们的任务就是专门负责与有关媒介单位接洽，安排有关广告的发播事宜，并对发播质量实施监督。</p>
      <table>
        <tr>
          <td>安全安保</td>
          <td>办公文教</td>
          <td>彩票</td>
          <td>车辆物流</td>
          <td>成人用品</td>
          <td>出版传媒</td>
          <td>电脑硬件</td>
        </tr>
        <tr>
          <td>电子电工</td>
          <td>房地产建筑装修</td>
          <td>分类平台</td>
          <td>服装鞋帽</td>
          <td>箱包饰品</td>
          <td>化工原料制品</td>
          <td>机械设备</td>
        </tr>
        <tr>
          <td>家庭日用品</td>
          <td>家用电器</td>
          <td>教育培训</td>
          <td>节能环保</td>
          <td>金融服务</td>
          <td>礼品</td>
          <td>旅游住宿</td>
        </tr>
        <tr>
          <td>美容化妆</td>
          <td>母婴护理</td>
          <td>农林牧渔</td>
          <td>软件</td>
          <td>商务服务</td>
          <td>生活服务</td>
          <td>食品保健品</td>
        </tr>
        <tr>
          <td>手机数码</td>
          <td>通讯服务设备</td>
          <td>网络服务</td>
          <td>医疗服务</td>
          <td>游戏</td>
          <td>运动休闲娱乐</td>
          <td>招商加盟</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import selection from '../components/selection'
import record from '../components/record'
import checkbox from '../components/checkbox'
import layout from '../components/layout'
import maskLayer from '../components/maskLayer'
export default {
  data: function () {
    return {
      checkboxParam: [
        {
          title: '初级版',
          id: 'chuji',
          type: 'checkbox',
          name: ''
        },
        {
          title: '中级版',
          id: 'zhongji',
          type: 'checkbox',
          name: ''
        },
        {
          title: '高级版',
          id: 'gaoji',
          type: 'checkbox',
          name: ''
        },
        {
          title: '专家版',
          id: 'zhuanjia',
          type: 'checkbox',
          name: ''
        }
      ],
      selectParam: ['出版业','媒体','金融','互联网','游戏'],
      gridData: [
        {
          num: 20,
          type:'出版业',
          time: '半年',
          version: '初级版',
          price: 500
        }
      ],
      isShadow: false,
      isLayout: false,
      isCheck: false,
      isSuccess: false,
      isFail: false,
      state: '',
      num: 5
    }
  },
  components: {
    selection,
    record,
    checkbox,
    layout,
    maskLayer
  },
  created: function(){
    this.menu();
  },
  methods: {
    menu: function(){
      window.scrollTo(0,0);
    },
    onCheckBoxVal: function(pra){
      this.gridData[0].version = pra.join(",");
    },
    onSelectVal: function(pra){
      this.gridData[0].type = pra;
    },
    onCloseLayout: function(pra){
      this.isLayout = pra;
      this.isShadow = pra;
    },
    onCheckLayout: function(pra){
      this.isCheck = pra;
      this.isShadow = pra;
    },
    onSuccessLayout: function(pra){
      this.isSuccess = pra;
      this.isShadow = pra;
    },
    onFailLayout: function(pra){
      this.isFail = pra;
      this.isShadow = pra;
    },
    onRecordEvent: function(pra){
      this.gridData[0].num = pra;
    },
    goRoute: function(){
      var self = this;
      let time = null;
      this.isCheck=false;
      this.state='success';
      this.isSuccess=true;
      time = setInterval(function(){
        if(self.num <= 0){
          clearInterval(time);
          self.$router.push('/oraderList');
        }else {
          self.num --;
        }
      },1000);
    }
  }
}
</script>

<style scoped>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

.detail-box table td {
  padding-left: 15px;
  border:1px solid #f0f2f5;
  height: 46px;
  line-height: 44px;
}
</style>



